body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ul { 
  list-style-type: none;
  display: -webkit-flex;
  display: flex;
}

li { margin-right: 1rem }

.jobList-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap
}

main { margin-top: 65px }

.header {
  background-color: #292972;
  color: whitesmoke;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  height: 65px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  -webkit-align-items: center;
          align-items: center;
  z-index: 1; }

form {
  margin: 3rem auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around; }
  form div + div {
    margin-top: 1em; }

fieldset {
  border-radius: 4px;
  border: solid 1px darkgray; }

label {
  display: inline-block;
  width: 20%;
  text-align: right;
  margin-right: 1rem; }

input, textarea {
  font: .8em sans-serif;
  width: 300px;
  box-sizing: border-box; }
  input:fous, textarea:fous {
    border-color: #000; }

input[type="date"] {
  width: 150px; }

textarea {
  vertical-align: top;
  resize: vertical;
  min-height: 50px; }

.button {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end; }

